import Vue from 'vue'
import Router from 'vue-router'
/**/

//
import carList from '@/page/mobile/info/info' //
import activity from '@/page/mobile/activity/activity' //
import loginList from '@/page/Login' //
import registerList from '@/page/Register' //
import passwordList from '@/page/Password'  //
import orderList from '@/page/mobile/order/order'  //
import paymentList from '@/page/mobile/payment/payment'  //
import fullList from '@/page/mobile/Full/full' //

import Index from '@/pc_page/index'
import news from '@/pc_page/news'
import temaihui from '@/pc_page/temaihui'
import xuanche from '@/pc_page/xuanche'

Vue.use(Router)

export default new Router({
	// router  mode hash  #! 
	mode: 'history',
  routes: [
    {
      path: '/',
      name: 'activity',
      component: activity
    },
    {
      path: '/carList/:id',
      name: 'carList',
      component: carList
    },
    
    {
      path: '/loginList',
      name: 'loginList',
      component: loginList
    },
    {
      path: '/registerList',
      name: 'registerList',
      component: registerList
    },
    {
      path: '/passwordList',
      name: 'passwordList',
      component: passwordList
    },
    {
      path: '/orderList',
      name: 'orderList',
      component: orderList
    },
    {
      path: '/paymentList',
      name: 'paymentList',
      component: paymentList
    },
    {
      path: '/fullList',
      name: 'fullList',
      component: fullList
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index
    },
    {
      path: '/news',
      name: 'news',
      component: news
    },
    {
      path: '/temaihui',
      name: 'temaihui',
      component: temaihui
    },
    {
      path: '/xuanche',
      name: 'xuanche',
      component: xuanche
    }
    
  ]
})



